<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ date1 }}</v-card-text>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Mond der Prinzessin",
    date1: "22.02.2016",
    unterkapitel1:
      'Ich war überwältigt von den riesigen Gebäuden aus Eis. Alles glänzte und schimmerte in allen Blautönen die man sich vorstellen kann. In dem kleinem Dorf, in dem ich aufwuchs gab es nur kleine Iglus. Ein reges Treiben beschäftigte die Stadt. Ich zog durch die Straßen immer in Richtung des Palasts, der erhaben über die Dächer der Stadt ragte. Ich wusste nicht was ich genau suchte. Ich würde wohl kaum einfach in den Palast spazieren um Fragen zu stellen. An einer Hauptstraße angekommen kam ich nicht weiter. Eine große Menschenmenge hielt sich an beiden Straßenrändern auf. Auf der Straße fuhren langsam 3 alabasterfarbende Kutschen entlang. Aus dem Augenwinkel sah ich wie ein Kind vorne in den Reihen seinen Ball fallen ließ, der dann direkt auf die Straße rollte. Als ich das Kind nach vorne springen sah, reagierte ich völlig kopflos. Ich machte einen riesigen Satz durch den Eisspeer, den ich unter mir wirkte. Es katapultierte mich knapp über die Menschenmenge hinweg und direkt neben das Kind, das ich gerade noch vor zu fassen bekam, bevor es unter das Rad der Letzten Kutsche geraten wäre. Die Kutsche stoppte abrupt. In einem Moment verstummte die ganze Straße. Die Tür der Kutsche ging auf. Ein Schleier silbrig weißen Haares glitzerte so hell, dass ich das Gesicht der Person nicht erkennen konnte. "Danke. Komm zu mir in die Kutsche." Ungläubiges Raunen ging durch die Menge, das Kind lief mit dem Ball zu seinem Vater und ich kletterte in die Kutsche.',
    unterkapitel2: '"Ist dir was passiert? Das war wirklich mutig!" Ich saß der Prinzessin der Wassernation gegenüber. "Mir geht es gut. Eure Majestät." Sie lächelte vornehm. "Nenn mich Siara. Es gibt nicht viele Menschen die so selbstlos reagieren. Kann ich dir irgendwas geben für deine Heldentat?" Das war meine Chance. Begeistert antwortete ich: "Ja da wäre was! Kennst du das Relikt des ersten Wasserbändigers?" Sie grinste vergnügt und fuhr sich mit ihrer zierlichen Hand durchs Haar. "Lass uns ein Spiel daraus machen. Ich erzähl dir was ich weiß, wenn du mir sagen kannst wie das Pferd des Avatars heißt." ',
    unterkapitel3: '"Ja Super! Das ist richtig!" Sie zappelte kurz glücklich auf ihrer Sitzbank, bis sie sich zu errinnern schien wer Sie ist. "OK, hör zu! Als Kind erzählte mir meine Meisterin oft, dass vor langer zeit jede Prinzessin eine Tiara trug. Sie wurde von Geburt zu Geburt von Prinzessin zu Prinzessin weitergegeben. Sie war aus Weißgold fein verarbeitet und trug einen gleißend weißen Edelstein. Sie soll heilende Kräfte verstärkt haben." Prizessin Siara senkte spielerisch die Stimme. "Doch irgendwann wurde die Tiara von Piraten gestohlen. Die Mondgöttin war entsetzt von der Habgier und versenkte das Schiff über einem uralten Tempel auf dem Grund des Meeres." So gebannt lauschte ich ihrer Stimme, dass ich ganz vergas wo ich war. Doch die Kutsche stopte und auch die Prinzessin schien aus ihrer eigenen Geschichte aufzuwachen. "Oh da haben wir uns verquatscht. Schön dich kennengelernt zu haben. Ich muss nun zum Unterricht. Geh du nach Tentacula und such dort nach dem besten Kapitain. Jeder seefahrende Wasserbändiger kennt diese Geschichte. Und wenn wir uns wieder sehen, darst du mir meine Tiara aufsetzen." Sie kicherte und stieg aus.',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "V0lSQkVMV0lORA==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

