<template>
  <v-flex xs8 offset-xs2>
    <v-layout justify-center>
      <v-card flat max-width="400">
        <v-card-title>
          <h2 class="headline">{{ headline }}</h2>
        </v-card-title>
        <v-card-text>{{ date1 }}</v-card-text>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel4 }}</v-card-text>

        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Suche nach Hinweisen",
    date1: "21.02.2016",
    unterkapitel1:
      'Ich schlug das Buch auf und fing an zu lesen. Walter schrieb auf den ersten Seiten viel alltägliches. Doch auf der siebten Seite fand ich es. Er sprach an diesem Tag mit einem leicht gebückt gehenden fremden vor der Bibliothek, der ihn nach dem Weg zur Feuernation fragte. Walter empfahl ihm das Boot von Captain Rangox im Hafen. Er würde umsonst mitfahren dürfen, wenn er sagte wer ihn schickt. Der Fremde sagte zu Walter: "Ich habe nichts was ich dir für deine Freundlichkeit geben kann und doch gebe ich dir das wertvollste was ich besitze. Ein kleines Stück Wissen, das vergessen ist und bald mit mir gestorben wäre. Republika hat ein Geheimnis. Gut versteckt und schwer zu finden. Wie das Wissen selbst ist es mächtiger als jedes Schwert. Geh in die Bibliothek und suche nach dem Buch der 4 Relikte. Es wird dich leiten."',
    unterkapitel2: 'Ich schlug das Tagebuch zu und blickte auf. Der Verkäufer des Standes guckte mich streng an. "Wenn du weiterlesen willst, musst du das Buch schon kaufen." Ohne zu verhandeln gab ich ihm was er verlangte und machte mich auf den Weg zur Bibliothek.',
    unterkapitel3: 'In der Bibliothek angekommen, suchte ich nach dem Buch der 4 Relikte.',
    unterkapitel4: 'Da war es! Das Buch der 4 Relikte. Schwer und wunderschön verziert fragte ich mich, ob ich es überhaupt aus der Bibliothek mitnehmen dürfte. Ich ging ins Erdgeschoss zum Schalter, vorbei an riesigen Regalen voll mit all dem Wissen über die Wunder der Welt. Über die Brille guckte mich ein Mädchen mit glattem schwarzen Haar an. Sie war gerade damit beschäftigt Bücher in Listen einzutragen. Im leicht geflüsterten, aber bestimmten Ton fragte sie: "Was möchtest du?". Leicht schüchtern antwortete ich: "Dieses Buch möchte ich leihen." Sie grinste mich an. "Geh zu Tobi. Von den schönsten Büchern hat er Abschriften gefertigt. er gibt dir eine mit. Diese Bücher sind nicht verleihbar." Man sah eine liebvolle Begeisterung in ihren Augen. Ich stammelte noch ein "Danke." raus und ging ins Büro auf das sie zeigte.',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "UkVMSUtURQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

<style>
