<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ date1 }}</v-card-text>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key=r>
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Das Relikt des ersten Wasserbändigers",
    date1: "22.02.2016",
    unterkapitel1:
      'Wo sollte ich jetzt nur anfangen? Ein Buch, 4 Relikte. Das erste Kapitel handelte von der Geschichte des Könighauses im Wasserreich. Doch kein Wort über ein Relikt. Nur ein Rätsel:',
    raetsel: ['"Die Tochter des Mondes', 'hell glänzender Ring', 'Tochter des Ozeans', 'den Mond auf der Stirn"'],
    unterkapitel2: 'Wo und wen könnte ich nach Hinweisen fragen?',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0lBUkE=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

