<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ date1 }}</v-card-text>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
          <v-card-text v-if="answered">{{ unterkapitel2 }}</v-card-text>
        <v-card-text v-if="answered">
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key=r>
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Captain ISn0w",
    date1: "22.02.2016",
    unterkapitel1:
      'Ein Pfad, kilometerweit geschlängelt über dem rauen Ozean. Gut, dass ich genug Proviant für diese Wanderung dabei hatte. Immer näher kam ich den dampfenden Wassersäulen, die mit so hohem Druck nach unten ins Meer schossen, dass sie eine ganze Stadt 100 Meter weit oben in der Luft trugen. Der Hafen unter der Stadt lag im wabernden Dampf der auf dem Ozean zerschellenden Wassersäulen. Ich trieb mich ein wenig umher und ging zum größten Schiff, das ich finden konnte.',
    unterkapitel2:
      'Die Wachen vor der Planke ließen mich aber nicht zum Kapitän, also ging ich in die Hafenkneipe um einen Tee zu trinken und ein paar Gespräche zu führen. Hinten in der Ecke, an einem runden Tisch saß ein Mann mit einem Bär. Ich ging hinüber und fragte nach dem besten Kapitän den er kennt. Er antwortete prompt und selbstsicher: "Er steht vor dir mein Freund. Captain ISn0w nennt man mich!" Im Hintergrund spielte sicher seit einer halben Stunde das gleiche Lied. Er sah erfahren aus, wirkte aber übermäßig stark von sich überzeugt und redete ständig mit seinem Bären, der jedoch immer nur blökte. Darum fragte ich: "Habt ihr denn auch ein Schiff?" "Ja. Das schnellste das je gebaut wurde. Aber kannst du es dir auch leisten?" Geld hatte ich kaum, also fragte ich nach einer Karte zum versunkenen Tempel. "Der Tempel der Mondgöttin nennt man ihn hier. Für kein Geld der Welt würde ich an diesen Ort fahren. Ich mag dich, also geb ich einer armen Seele einen Tipp, wie du ihn findest, falls er existiert. Ein Teil der Legende ist ein Sprichwort. “',
    raetsel: ['"Der Mond geht auf, doch nicht zu Land.', 'Der Mond geht unter, auf Grund der See.', 'Der Weg durchs Wasser im dunklen beschrieben.',
     'Grünes Schimmern.', 'Sanftes leuchten.', 'Finde den Weg unter Bögen im tiefen Blau."'],
    unterkapitel3: 'ISn0w zuckte lässig mit den Schultern. “Also wenn du mich fragst, klingt das verdächtig nach der Unterwasserstadt Serenity Valley. Du! Wenn du dabei drauf gehst, beschwer dich nicht bei mir!”',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0VSRU5JVFk=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

<style>
</style>
